import { Link, useOutletContext, useNavigate } from "react-router-dom";
import { useForm, useWatch } from "react-hook-form";
import { Input, Select, CheckboxRadio } from "../../components/FormElements";
import { useEffect, useState } from "react";
import Loading from "../../components/Loading";
import axios from "axios";

const localAxios = axios.create({
  baseURL: "",
});

function Checkout() {
  const { cartData } = useOutletContext();
  const [addressData, setAddressData] = useState([]);
  const [districts, setDistricts] = useState([]);
  const [paymentMethod, setPaymentMethod] = useState("");
  const [userEmail, setUserEmail] = useState("");
  const [fullAddress, setFullAddress] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const {
    register,
    handleSubmit,
    getValues,
    control,
    formState: { errors },
  } = useForm({ mode: "onTouched" });
  const navigate = useNavigate();

  const onSubmit = async (data) => {
    const { name, email, tel } = data;

    const form = {
      data: {
        user: {
          name,
          email,
          tel,
          address: fullAddress,
        },
      },
    };
    try {
      setIsLoading(true);
      const res = await axios.post(
        `v2/api/${process.env.REACT_APP_API_PATH}/order`,
        form
      );
      // console.log(res);
      setIsLoading(false);
      scrollToTop();
      navigate(`/cart/checkout-confirm/${res.data.orderId}`);
    } catch (error) {
      setIsLoading(false);
      scrollToTop();
      console.log(error);
    }
  };

  const watchEmail = useWatch({
    control,
    name: "email",
  });

  const watchCity = useWatch({
    control,
    name: "city",
  });
  const watchDistrict = useWatch({
    control,
    name: "district",
  });
  const watchAddress = useWatch({
    control,
    name: "address",
  });

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  useEffect(() => {
    async function getAddressData() {
      try {
        const response = await localAxios.get(
          `${process.env.PUBLIC_URL}/assets/taiwan.json`
        );
        setAddressData(response.data);
      } catch (error) {
        console.error(error);
      }
    }

    getAddressData();
  }, []);

  useEffect(() => {
    //    
    if (watchCity) {
      const selectedCity = addressData.find(
        (item) => item.CityName === watchCity
      );
      if (selectedCity) {
        setDistricts(selectedCity.AreaList);
      }
    } else {
      setDistricts([]);
    }
  }, [watchCity, addressData]);

  useEffect(() => {
    //  city, district, address 
    setFullAddress(
      `${watchCity || ""}${watchDistrict || ""}${watchAddress || ""}`.trim()
    );
  }, [watchCity, watchDistrict, watchAddress]);

  useEffect(() => {
    //  Email
    setUserEmail(watchEmail);
  }, [watchEmail]);

  useEffect(() => {
    // ,  API 
    localStorage.setItem("paymentMethod", paymentMethod);
    // console.log(localStorage.getItem("paymentMethod"));
  }, [paymentMethod]);

  return (
    <div className='container'>
      <Loading isLoading={isLoading} />
      <div className='pt-5 pb-7'>
        <div className='container'>
          <Link
            to='/cart'
            className='btn text-dark py-3 mt-md-0 mt-3'
            onClick={() => scrollToTop()}
          >
            <i className='bi bi-arrow-left'></i> 
          </Link>

          <div className='row flex-md-row'>
            {/*  */}
            <div className='col-lg-4 col-12 bg-white pt-4 pb-5 pt-lg-0 cart-summery-container'>
              <div className='d-flex text-bg-dark rounded-top'>
                <h4 className='m-2'></h4>
              </div>
              <div className='bg-light rounded-bottom p-4'>
                {cartData?.carts?.map((item) => {
                  return (
                    <div className='d-flex mt-2' key={item.id}>
                      <img
                        className='rounded-2 me-2'
                        src={item.product.imageUrl}
                        alt=''
                        style={{
                          width: "48px",
                          height: "48px",
                          objectFit: "cover",
                        }}
                      />
                      <div className='w-100'>
                        <div className='d-flex justify-content-between fw-bold'>
                          <p className='mb-0'>{item.product.title}</p>
                          <p className='mb-0'>x {item.qty}</p>
                        </div>
                        <div className='d-flex justify-content-between'>
                          <p className='text-muted mb-0'>
                            <small>NT$ {item.product.price}</small>
                          </p>
                          <p className='mb-0'>NT$ {item.final_total}</p>
                        </div>
                      </div>
                    </div>
                  );
                })}

                <table className='table mt-4 border-top border-bottom'>
                  <tbody>
                    <tr>
                      <th
                        scope='row'
                        className='border-0 px-0 pt-4 font-weight-normal'
                      >
                        
                      </th>
                      <td className='text-end border-0 px-0 pt-4'>
                        NT$ {cartData.final_total}
                      </td>
                    </tr>
                    <tr>
                      <th
                        scope='row'
                        className='border-0 px-0 pt-0 pb-4 font-weight-normal'
                      >
                        
                      </th>
                      <td className='text-end border-0 px-0 pt-0 pb-4'>
                        {paymentMethod}
                      </td>
                    </tr>
                  </tbody>
                </table>
                <div className='d-flex justify-content-between mt-4'>
                  <p className='mb-0 h4 fw-bold'></p>
                  <p className='mb-0 h4 fw-bold'>NT$ {cartData.final_total}</p>
                </div>
              </div>

              <div className='bg-light rounded-2 mt-3 p-4'>
                <h5 className='mb-2'>
                  <strong></strong>
                </h5>
                <p>
                  
                  <br />
                  
                  <br />
                  
                  <br />
                  
                  <br />
                </p>
                <p>
                  
                </p>
                <p>
                  
                  <br />
                  
                </p>
                <br />
                
              </div>
            </div>

            {/*  */}
            <div className='col-lg-8 col-12'>
              <div className='d-flex text-bg-dark rounded-top'>
                <h4 className='m-2'></h4>
              </div>
              <div className='bg-light p-4 rounded-bottom'>
                <form onSubmit={handleSubmit(onSubmit)}>
                  <div className='mb-2'>
                    <Input
                      id='name'
                      type='text'
                      errors={errors}
                      labelText=''
                      register={register}
                      rules={{
                        required: "",
                        maxLength: {
                          value: 10,
                          message: " 10",
                        },
                      }}
                    ></Input>
                  </div>
                  <div className='mb-2'>
                    <Input
                      id='email'
                      labelText=''
                      type='email'
                      errors={errors}
                      register={register}
                      rules={{
                        required: "Email ",
                        pattern: {
                          value: /^\S+@\S+$/i,
                          message: "Email ",
                        },
                      }}
                    ></Input>
                  </div>
                  <div className='mb-2'>
                    <Input
                      id='confirmEmail'
                      labelText=''
                      type='email'
                      errors={errors}
                      register={register}
                      rules={{
                        required: " Email ",
                        pattern: {
                          value: /^\S+@\S+$/i,
                          message: "Email ",
                        },
                        validate: (value) =>
                          value === userEmail || " Email",
                      }}
                    ></Input>
                  </div>
                  <div className=''>
                    <Input
                      id='tel'
                      labelText=''
                      type='tel'
                      errors={errors}
                      register={register}
                      rules={{
                        required: "",
                        minLength: {
                          value: 10,
                          message: "",
                        },
                        maxLength: {
                          value: 10,
                          message: "",
                        },
                      }}
                    ></Input>
                  </div>
                </form>
              </div>

              {/*  */}
              <div className='mt-3'>
                <div className='d-flex text-bg-dark rounded-top'>
                  <h4 className='m-2'></h4>
                </div>
                <div className='bg-light p-4 rounded-2'>
                  <form onSubmit={handleSubmit(onSubmit)}>
                    <div className='form-row'>
                      <div className='col mb-2'>
                        <Select
                          id='city'
                          labelText=''
                          defaultValue=''
                          errors={errors}
                          register={register}
                          rules={{
                            required: "",
                          }}
                        >
                          <option value='' disabled>
                            
                          </option>
                          {addressData.map((city) => {
                            return (
                              <option value={city.CityName} key={city.CityName}>
                                {city.CityName}
                              </option>
                            );
                          })}
                        </Select>
                      </div>
                      <div className='col mb-2'>
                        <Select
                          id='district'
                          labelText=''
                          errors={errors}
                          register={register}
                          disabled={!getValues().city}
                          rules={{
                            required: "",
                          }}
                        >
                          <option value='' disabled={getValues().city}>
                            
                          </option>
                          {districts.map((district) => (
                            <option
                              value={district.AreaName}
                              key={district.AreaName}
                            >
                              {district.AreaName}
                            </option>
                          ))}
                        </Select>
                      </div>
                    </div>
                    <Input
                      id='address'
                      labelText=''
                      type='address'
                      errors={errors}
                      register={register}
                      rules={{
                        required: "",
                      }}
                    ></Input>
                    <p className='mt-4 mb-2'></p>
                    <div className='mb-2 ps-0'>
                      <CheckboxRadio
                        type='radio'
                        name='payment'
                        id='paymentATM'
                        labelText='ATM'
                        value={true}
                        register={register}
                        errors={errors}
                        rules={{
                          required: "",
                        }}
                        onChange={() => setPaymentMethod("ATM")}
                      ></CheckboxRadio>
                    </div>
                    <div className='mb-2 ps-0'>
                      <CheckboxRadio
                        type='radio'
                        name='payment'
                        id='paymentCreditCard'
                        labelText=''
                        value={true}
                        register={register}
                        errors={errors}
                        rules={{
                          required: "",
                        }}
                        onChange={() => setPaymentMethod("")}
                      ></CheckboxRadio>
                    </div>
                    <div className='mt-4 pt-3 border-top'>
                      <CheckboxRadio
                        type='checkbox'
                        name='isCheckForm'
                        id='isCheckForm'
                        value={true}
                        register={register}
                        errors={errors}
                        rules={{ required: "" }}
                        labelText=''
                      ></CheckboxRadio>
                    </div>
                    <button
                      type='submit'
                      className={`btn btn-primary mt-3 py-3 w-100 rounded-2 ${
                        isLoading
                          ? "button_addToCart btn btn-primary w-100 rounded-2 py-3 bounceBack"
                          : "button_addToCart btn btn-primary w-100 rounded-2 py-3"
                      }`}
                      disabled={isLoading}
                    >
                      {isLoading ? "..." : ""}
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Checkout;
